.progressBar {
    position: relative;
    width: 100%;
    height: 4px;
    border-radius: 15px;
    top: -33px;
    background-color: rgba(229, 241, 239, 1);
  }
  
  .progress {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 4px;
    border-radius: 15px;
    transition: width .5s linear;
    background-color: rgba(229, 241, 239, 1);
  }