:root {
  --text-primary: black;
  --text-alernative: white;
  --text-placeholder-input-grey-color: rgba(189, 189, 189, 1);
  --text-white-color: rgba(255, 255, 255, 1);
  --text-grey-color: rgba(166, 166, 166, 1);
  --text-red-color: rgba(255, 101, 150, 1);
  --text-green-color: rgba(36, 204, 167, 1);

  --main-background-color: rgba(255, 255, 255, 1);
  --secondary-background-color: rgba(255, 255, 255, 0.4);

  --right-side-red-line-status: rgba(255, 101, 150, 1);
  --right-side-green-line-status: rgba(36, 204, 167, 1);
   // icons

   --main-accent-color-blue: #4a56e2;
   --main-accent-filter: drop-shadow(0px 3px 10px rgba(74, 86, 226, 0.5));
   --secondary-accent-color-blue: rgba(110, 120, 232, 1);
   --grey-color-icon: rgba(189, 189, 189, 1);
 
   // Modal window
   --text-modal-green-color: rgba(36, 204, 167, 1);
   --text-modal-grey-color: rgba(224, 224, 224, 1);
   --text-modal-red-color: rgba(255, 101, 150, 1);
 
   --modal-background-color-white: rgba(255, 255, 255, 1);
   --modal-background-color-grey: rgba(0, 0, 0, 0.25);
 
   --modal-currency-blue: rgba(74, 86, 226, 1);
    // Dropdown

  --background-color-dropdown: rgba(255, 255, 255, 0.7);
  --shadow-dropdown: 0px 6px 15px rgba(0, 0, 0, 0.1);
  --filter-dropdown: blur(25px);
  --backgroud-color-dropdown-second: #f6f0ef;
  --filter-dropdown-display-none: blur(2px);
   // Buttons
   --btn-green-color: #24cca7;
   --btn-blue-color: rgba(74, 86, 226, 1);
 
   --btn-text-white: rgba(255, 255, 255, 1);
   --btn-text-blue: rgba(74, 86, 226, 1);
 
   --green-circle-shadow: 0px 6px 15px rgba(36, 204, 167, 0.5);
   --red-circle-shadow: 0px 6px 15px rgba(255, 101, 150, 0.5);
 
   --btn-green-color-hover: rgb(115, 248, 219);
   --btn-green-color-active: rgb(38, 177, 147);
 
   --btn-blue-color-hover: rgb(124, 133, 235);
   --btn-blue-color-active: rgb(29, 41, 177);
     // table
  --grey-bottom-border-table: rgba(220, 220, 223, 1);
  --box-shadow-table: rgba(255, 255, 255, 0.6);

  // Backgrounds
  --background-white-color: rgba(255, 255, 255, 1);
  --background-grey-color: rgba(255, 255, 255, 0.4);

    // Adaptive width
    --mobile: 480px;
    --tablet: 768px;
    --desktop: 1280px;

  // --textShadow-primary: 2px 2px 2px black;

  // --link-primary: rgb(193, 152, 30);
  // --link-secondary: rgb(255, 229, 151);

  // --bg-primary: rgb(255, 247, 224);
  // --bg-secondary: rgb(248, 236, 198);

  // --bgShadow-primary: linear-gradient(
  //   rgba(175, 174, 173, 0.3),
  //   rgba(175, 174, 173, 0.3)
  // );
  // --bgShadow-secondary: linear-gradient(
  //   rgba(175, 174, 173, 0.15),
  //   rgba(175, 174, 173, 0.15)
  // );

  // --border-primary: rgb(255, 220, 107);
}
